/* social media icon start */

.social-menu ul {
  position: absolute;
  
  display: flex;
}
.social-menu ul li {
  list-style: none;
  margin: 0 5px;
}
.social-menu ul li .fa {
  color: #000000;
  font-size: 18px;
  line-height: 50px;
  margin-top: 4px;
  transition: .5s;
}
.social-menu ul li .fa:hover {
  color: #ffffff;
}
.social-menu ul li .fs {
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #656565;
  text-align: center;
  transition: 0.5s;
  transform: translate(0,0px);
  box-shadow: 0px 7px 5px rgba(0, 0, 0, 0.5);
}
.social-menu ul li .fs:hover {
  transform: rotate(0deg) skew(0deg) translate(0, -10px);
}
.social-menu ul li:nth-child(1) .fs:hover {
  background-color: #3b5999;
}
.social-menu ul li:nth-child(2) .fs:hover {
  background-color: #55acee;
}
.social-menu ul li:nth-child(3) .fs:hover {
  background-color: #e4405f;
}
.social-menu ul li:nth-child(4) .fs:hover {
  background-color: #cd201f;
}
.social-menu ul li:nth-child(5) .fs:hover {
  background-color: #0077B5;
}





